import { requireDetailer } from '@/lib/auth';
import { createClient } from '@/lib/supabase/server';
import SettingsPageClient from './SettingsPageClient';

export default async function SettingsPage() {
  const profile = await requireDetailer();
  const supabase = await createClient();

  // Get detailer record
  const { data: detailerData } = await supabase.rpc('get_detailer_by_profile', {
    p_profile_id: null,
  });

  // Get service areas
  let serviceAreas: any[] = [];
  if (detailerData?.id) {
    const { data } = await supabase
      .from('service_areas')
      .select('*')
      .eq('detailer_id', detailerData.id)
      .eq('is_active', true);
    serviceAreas = data || [];
  }

  // Get notification settings
  const { data: notificationSettings } = await supabase
    .from('notification_settings')
    .select('*')
    .eq('user_id', profile.id)
    .single();

  return (
    <div className="min-h-screen bg-[#050B12] text-white">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white mb-2">Settings</h1>
          <p className="text-[#C6CFD9]">Manage your profile and preferences</p>
        </div>

        <SettingsPageClient
          profile={profile}
          serviceAreas={serviceAreas}
          notificationSettings={notificationSettings}
        />
      </div>
    </div>
  );
}

